
    <template>
      <section>
        <div>
          <div class="col-lg-12 border p-2 float-left">
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
              <div class="row cal_iconsbase" id="divHeader">
                  <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                      <div class="row label_group">
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('CALocation')}} :</b><span class="lighttext"> {{ this.AssetCalendarData[0].locatioN_NAME }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('CRM_PRODUCTS')}} : </b><span class="lighttext"> {{ this.AssetCalendarData[0].name }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('Total')}} : </b><span class="lighttext" > {{ this.AssetCalendarData[0].quantity }}</span></div> </div>
                          <div class="col-lg-6"><div class="form-group"><b>{{$t('UniqueName')}} : </b><span class="lighttext"> {{ this.AssetCalendarData[0].uniquE_NAME }}</span></div> </div>                        </div>
                        
                  </div>                  
              </div>
          </div>
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0 mt-2">
              <div class="table-responsive">
                <calendar :events="events" />
              </div>
          </div>
      </div>
        </div>
      </section>
    </template>
 <!-- <template>
   <div id="app">
     <div class="main">
       <div class="calendar-holder">
         <calendar :events="events" />
       </div>
     </div>
   </div>
 </template> -->


 <script>
 
import DataService from '../../service/DataService';
import Calendar from '../AssetCatalog/Calender.vue'
//  import Pusher from 'pusher-js';
 export default {
   name: 'app',
   components: {
     Calendar
   },
   data(){
     return {
       events: [{
         title     :  '',
         start     : '',
         cssClass  : '',
       }
      ],
       AssetCalendarData:[], 
       attributes: [],  
     }
   },
   created:function(){
    this.LoadAssetCalendar();
   },
   methods :{
    LoadAssetCalendar:function(){ 
        debugger
          var url=`userId=0&&locationId=${this.$route.params.lid}&assetCatId=${this.$route.params.aid}&rid=${this.$route.params.rid}&start=&end=&requestedEventType=`;
          DataService.LoadAssetCalendar(url).then(response=>{              
              this.AssetCalendarData = response.data;                
              this.AssetCalendarData.forEach((item,index)=>{
                if(item.title == 'AVAILABLE'){              
                this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['green']
                })           
                }
                else if(item.title == 'RESERVED'){                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['Yellow']
                })
                }else if(item.title == 'ASSIGNED'){                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['Black']
                })
                } else if(item.title == 'IN-TRANSIT'){                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['Pink']
                })
                } else if(item.title == 'AUDIT (annual )'){                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['blue']
                })
                } else if(item.title == 'CALIBRATION (quarterly )'){                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['blue']
                }) 
                } else {                    
                    this.events.push({
                    title:item.title+'('+item.quantity+')',start:item.start,end:item.end,cssClass:['purple']
                }) 
                }                                   
              });
          });       
      }
   }
 }
 </script>
